@import "helpers/variables";
@import "helpers/mixins";
@import "helpers/functions";

@import "../blocks/modules/home/home-contacts/home-contacts.scss";

.contacts {
  padding: 20px 0 0 0;

  .container {
    position: relative;
  }

  &-figure {
    position: absolute;
    display: none;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    @media screen and (min-width: 480px) {
    }

    @media screen and (min-width: 640px) {
    }

    @media screen and (min-width: 960px) {
      display: block;
      width: 421px;
      height: 360px;
      top: -60px;
      right: -96px;
      z-index: -1;
    }

    @media screen and (min-width: 1152px) {
      top: -120px;
      right: -192px;
    }


  }

  &-props {
    width: 100%;

    .homeContacts-items__item {
      width: 100%;
    }

  }

  @media screen and (min-width: 640px) {
    padding: 40px 0 0 0;

    .section-h2 {
      margin-bottom: 30px;
    }

  }

  @media screen and (min-width: 1152px) {
    padding: 80px 0 0 0;

    .section-h2 {
      margin-bottom: 70px;
    }

  }


  .section-h2 {
    margin-bottom: 20px;
    width: 100%;
    text-align: center;
  }

  @media screen and (min-width: 480px) {

    &-props {
      .homeContacts-items__item {
        width: calc(100% - 20px);
      }

    }


  }

  @media screen and (min-width: 960px) {
    .section-h2 {
      text-align: left;
    }

    .container {
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
    }

    &-props, &-info {
      width: calc(50% - 10px);
      margin: 0;
    }

    &-props {
      align-self: stretch;

      .homeContacts-items__item {
        width: 100%;
        margin: 0 0 20px;
      }
    }

    &-info {
      justify-content: space-between;

      .homeContacts-items__item {
        width: calc(50% - 10px);
        margin: 0 0 20px;
      }
    }

    &-map {
      margin-top: 20px;
    }

  }

  @media screen and (min-width: 1152px) {

    &-props, &-info {
      width: calc(50% - 20px);
      margin: 0;
    }

    &-props {
      align-self: stretch;

      .homeContacts-items__item {
        width: 100%;
        margin: 0 0 40px;
      }
    }

    &-info {
      justify-content: space-between;

      .homeContacts-items__item {
        width: calc(50% - 20px);
        margin: 0 0 40px;
      }
    }

    &-map {
      .homeContacts-contacts__map {
        padding-top: 800px;
      }
    }

  }

}